import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';


import { DialogInterface } from '../../../models/DialogInterface';
import { StateserviceService } from '../../../services/shared/stateservice.service';

@Component({
  selector: 'ngx-confirmalert',
  templateUrl: './confirmalert.component.html',
  styleUrls: ['./confirmalert.component.scss']
})
export class ConfirmalertComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<ConfirmalertComponent>,
    @Inject(MAT_DIALOG_DATA)
    public dialogData: DialogInterface,
    public stateService: StateserviceService
  ) { }
  ngOnInit(): void { }

  handleDialogSubmit() {
    this.stateService.isAsyncOperationRunning$.next(true);
    setTimeout(() => {
      this.dialogData.callbackMethod();
      this.stateService.isAsyncOperationRunning$.next(false);
    }, 10);
    this.dialogRef.close();
  }
  closeDialog(): void {
    this.dialogRef.close();
  }
}
