import { Component } from '@angular/core';
import { NbIconLibraries } from '@nebular/theme';
declare interface RouteInfo {
  path: string;
  title: string;
  icon: string;
  class: string;
}
@Component({
  selector: 'ngx-one-column-layout',
  styleUrls: ['./one-column.layout.css'],
  templateUrl: './one-column.layout.html',
})

export class OneColumnLayoutComponent {



  constructor(
    private iconLibraries: NbIconLibraries) {
    //  this.iconLibraries.registerFontPack('fa', { iconClassPrefix: 'fa' });
      this.iconLibraries.registerFontPack('font-awesome', { iconClassPrefix: 'fa', packClass: 'fas' });
      this.iconLibraries.registerFontPack('far', { iconClassPrefix: 'fa', packClass: 'far' });
    }
  ngOnInit() {

  }
}
