import { Component, OnDestroy, OnInit } from '@angular/core';
import { NbMediaBreakpointsService, NbMenuService, NbSidebarService, NbThemeService } from '@nebular/theme';

import { UserData } from '../../../@core/data/users';

import { map, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { Router } from '@angular/router';

@Component({
  selector: 'ngx-header',
  styleUrls: ['./header.component.scss'],
  templateUrl: './header.component.html',
})
export class HeaderComponent implements OnInit, OnDestroy {

  private destroy$: Subject<void> = new Subject<void>();
  userPictureOnly: boolean = false;
  user: any;

  themes = [
    {
      value: 'default',
      name: 'Light',
    }
  ];

  currentTheme = 'default';

  userMenu = [{ title: 'Log out', icon: { icon: 'sign-out-alt', pack: 'font-awesome' },  link: '/login',}];
 
  constructor(private sidebarService: NbSidebarService,
    private menuService: NbMenuService,
    private themeService: NbThemeService,
    private userService: UserData,private route: Router,
    public translate: TranslateService

  ) {
    translate.addLangs(['AR', 'FR']);
    console.log(localStorage.getItem('lang'));
    if (localStorage.getItem('lang')) {
      this.translate.use(localStorage.getItem('lang'));
    } else {
      this.translate.use('FR');
      const browserLang = translate.getBrowserLang();
      translate.use(browserLang.match(/AR|FR/) ? browserLang : 'FR');
    }


   
  }



  ngOnInit() {
    this.currentTheme = this.themeService.currentTheme;

    this.userService.getUsers()
      .pipe(takeUntil(this.destroy$))
      .subscribe((users: any) => this.user = users.nick);




  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  /**
   * Cette fonction permet le changement de la langue
   * @param lang 
   */
  switchLang(lang: string) {
    this.translate.use(lang);
    localStorage.setItem('lang', lang);

  }

  toggleSidebar(): boolean {
    this.sidebarService.toggle(true, 'menu-sidebar');


    return false;
  }

  navigateHome() {
    this.menuService.navigateHome();
    return false;
  }

  Logout(){
    this.route.navigateByUrl('/login');
  }
}

